@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap);
* {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
}

body {
  background-image: url("https://images.unsplash.com/photo-1505751172876-fa1923c5c528?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=3543&q=80");
  background-repeat: no-repeat;
  background-position: center;
}
.profile-container {
    display: grid;
    grid-gap: 2rem;
    grid-template-rows: 1fr 10fr 1fr;
    color: white;
    position: absolute;
    background-color: #26264a;
    width: 95vw;
    height: 95vh;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.profile-container{
    align-items: start;
}

.profile-container button {
    color: white;
    width: 20em;
    height: 5em;
    background-color: #2E2F30;
}

.profile-container button:hover{
    background-color: #4183C4;
    transition: .5s;
}

.profile-container button:focus {
    outline: none;
}

#profile-navbar {
    background-color: #292929;
    padding: 2rem;
}
.auth-action-container {
    padding: 3em;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: white;
    width: 20em;
    height: 20em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #26264a;
}

.auth-action-container button {
    margin-top: 10em;
    color: white;
    padding: 2em;
    background-color: #2E2F30;
}

.auth-action-container button:hover{
    background-color: #4183C4;
    transition: .5s;
}

.auth-action-container button:focus {
    outline: none;
}
